import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { TableData, ColEditDelete } from 'components/common';
import { getListSubjectGroup, deleteSubjectGroup, getFaculty } from 'services/subjectGroup';
import { Card, Row, Col, message } from 'antd';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA = routeConstant.DANH_MUC__DAO_TAO__BO_MON__NHAP_DU_LIEU;

class SubjectPage extends Component {
  state = {};

  async componentWillMount() {
    const dataFaculty = await getFaculty();
    const listDataFaculty = {};
    dataFaculty.data.items.forEach(item => {
      listDataFaculty[item.id.toString()] = item;
    });
    this.setState({
      listDataFaculty,
    });
  }

  handleDelete = async id => {
    await deleteSubjectGroup(id);
    message.success('Xóa thành công');
    this.setState({
      reload: this.state.reload + 1,
    });
  };

  render() {
    const { listDataFaculty } = this.state;

    const columns = [
      {
        title: 'Mã bộ môn',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên bộ môn',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Khoa',
        dataIndex: 'facultyId',
        key: 'facultyId',
        render: (text, record) => (
          <div>{listDataFaculty ? listDataFaculty[record.facultyId].label : ''}</div>
        ),
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: ROUTER_FORM_DATA,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    const { reload } = this.state;
    return (
      <Card title="Danh mục bộ môn">
        <Row>
          <Col span={24}>
            <Link to={ROUTER_FORM_DATA}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            {listDataFaculty ? (
              <TableData
                reload={reload}
                service={getListSubjectGroup}
                columns={columns}
                rowKey={record => record.label + record.id}
              />
            ) : null}
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <SubjectPage />
  </Page>
);



